import { Fragment, useState, useEffect } from 'react'
import { ThemeProvider } from '@material-ui/styles'
import { ethers } from 'ethers'
import { 
  artBotBrokerAddress, 
  getEventsForUser as getArtBlocksEvents, 
  getLatestOrdersForUser as getArtBlocksOrders, 
  getUserHistory as getArtBlocksUserHistory 
} from './scripts/helpers'

import {
  ERC721LimitOrderAddress,
  getEventsForUser as getNFTEvents,
  getLatestOrdersForUser as getNFTOrders,
  getUserHistory as getNFTHistory
} from './scripts/generalizedERC721Helpers'

import BaseTheme from './theme.js'
import './App.css'

import { Container } from '@material-ui/core'
import CssBaseline from "@material-ui/core/CssBaseline";
import CheckCircleIcon from '@material-ui/icons/CheckCircle'

import Spinner from './components/Spinner'
import Connect from './components/Connect'
import BidForm from './components/BidForm'
import PendingTx from './components/PendingTx'

import artBotBrokerAbi from './abis/ArtBlocksBroker'
import ERC721LimitOrderAbi from './abis/ERC721LimitOrder'

export default function App() {

  // Connection info
  const [ wallet, setWallet ] = useState({
    provider: null,
    signer: null,
    connected: false,
    signerAddress: null,
    disabled: false,
    pending: false,
    error: ''
  })
  
  const [ metamaskInstalled, setMetamaskInstalled ] = useState(true)

  // List of NFT project addresses that we show in dropdown.
  // - Add project name and corresponding address here.
  // - Set "active" to false when the project should no longer be listed in the Bid Form
  // - Do not remove items from this list because they are used in User Orders and Bid History
  // even after a project has launched
  const [ NFTAddresses ] = useState([
    {
      'name': 'Art Blocks', // Do not change this name. It is used to determine if Project ID is required
      'contract': '0xa7d8d9ef8D8Ce8992Df33D8b8CF4Aebabd5bD270',
      'url': '',
      'active': true,
    },
    {
      'name': 'Crypto Dads',
      'contract': '0xECDD2F733bD20E56865750eBcE33f17Da0bEE461',
      'url': 'https://www.cryptodadsnft.com/',
      'active': false,
    },
    {
      'name': 'The Sevens',
      'contract': '0xf497253C2bB7644ebb99e4d9ECC104aE7a79187A',
      'url': 'https://thesevensofficial.com/',
      'active': false,
    },
    {
      'name': 'The Wicked Craniums',
      'contract': '0x85f740958906b317de6ed79663012859067E745B',
      'url': 'https://wickedcranium.com/',
      'active': false,
    },
    {
      'name': 'Other',
      'contract': '',
      'url': '',
      'active': false,
    },
  ])

  // User's bid input data
  const [ bidFields, setBidFields ] = useState({
    projectSelect: '',
    projectSelectError: '',
    contractAddress: '',
    contractAddressError: '',
    projectId: '',
    projectIdRequired: false,
    projectIdError: '',
    quantity: '',
    quantityError: '',
    bid: '',
    bidError: '',
    generalError: '',
    pending: false,
    button: 'Place Bid'
  })

  // Pending transaction notifications
  const [ pendingTxs, setPendingTxs ] = useState([
    // {
    //   projectName: 'Pilones',
    //   quantity: '2',
    //   bid: '1.2592',
    //   hash: '0x8b132b821b4f308fcf0ce60b0c2373e8b8fc5a19bc5abd2973e059ff11dc0a25',
    //   type: 'Placed',
    //   status: 'pending'
    // },
    // {
    //   projectName: 'Lunar Eclipse',
    //   quantity: '4',
    //   bid: '0.25',
    //   hash: '0x8b132b821b4f308fcf0ce60b0c2373e8b8fc5a19bc5abd2973e059ff11dc0a25',
    //   type: 'Canceled',
    //   status: 'failed'
    // }
  ])

  // List of user orders
  const [ userOrders, setUserOrders ] = useState([])
  const [ userOrderState, setUserOrderState ] = useState('')

  const [ userHistory, setUserHistory ] = useState([])
  const [ userHistoryState, setUserHistoryState ] = useState('')

  function addPendingTx(projectName, quantity, bid, hash, type) {
    setPendingTxs(prevPendingTxs => {
      return prevPendingTxs.concat({
        projectName,
        quantity,
        bid,
        hash,
        type,
        status: 'pending'
      })
    })
  }

  function updatePendingTxStatus(hash, status) {
    setPendingTxs(prevPendingTxs => {
      return prevPendingTxs.map((txItem, index) => {
        if (txItem.hash === hash) {
          return { ...txItem, status }
        } else {
          return txItem
        }
      })
    })

    // Hide the pending tx box after a couple seconds
    window.setTimeout(() => { 
      setPendingTxs(prevPendingTxs => {
        return prevPendingTxs.filter(txItem => txItem.hash !== hash)
      })
    }, 5000)
  }

  async function getUserOrders() {
    setUserOrderState(prevState => 'pending')
    try {
      if (wallet.connected) {
        const artBlockEvents = await getArtBlocksEvents(wallet.signerAddress) //await getArtBlocksEvents('0x1A67911B5c07B16529bfDD5d8D5F34080Db2096A') //
        const artBlocksOrders = await getArtBlocksOrders(wallet.signerAddress, artBlockEvents) //await getArtBlocksOrders('0x1A67911B5c07B16529bfDD5d8D5F34080Db2096A', eventsForUser) //

        const NFTEvents = await getNFTEvents(wallet.signerAddress) // await getNFTEvents('0xc87F98bc399FD19f86AfFAE85D80e150CE92F170') // 
        const NFTOrders = await getNFTOrders(wallet.signerAddress, NFTEvents) // await getNFTOrders('0xc87F98bc399FD19f86AfFAE85D80e150CE92F170', NFTEvents) // 

        let allUserOrders = []

        // Add art blocks orders
        allUserOrders = allUserOrders.concat(artBlocksOrders.map(order => { 
          return { 
            name: `Art Blocks #${order.artBlocksProjectId.toString()}`, 
            id: order.artBlocksProjectId.toString(),
            type: 'Art Blocks',
            quantity: order.quantity.toString(), 
            bid: order.priceInWeiEach.toString() / 1e18, 
            status: 'placed',
            text: 'cancel' 
          }
        })) // .sort((first, second) => (first.projectId > second.projectId) ? -1 : 1))

        // Add all other orders
        allUserOrders = allUserOrders.concat(NFTOrders.map(order => {
          const NFTProject = NFTAddresses.filter(address => address.contract === order.tokenAddress)
          return { 
            name: NFTProject[0].name, 
            id: order.tokenAddress,
            type: 'General',
            quantity: order.quantity.toString(), 
            bid: order.priceInWeiEach.toString() / 1e18, 
            status: 'placed',
            text: 'cancel' 
          }
        }))

        // Sort in alphabetical order
        allUserOrders.sort((first, second) => (first.name.toUpperCase() < second.name.toUpperCase()) ? -1 : 1)

        setUserOrders(prevOrders => allUserOrders)
        setUserOrderState(prevState => '')
      }
    } catch(err) {
      console.log(err)
      setUserOrderState(prevState => 'error')
    }
  }

  async function getUserHistory() {
    setUserHistoryState(prevState => 'pending')
    try {
      if (wallet.connected) {
        const artBlockEvents = await await getArtBlocksEvents(wallet.signerAddress) // getArtBlocksEvents('0xc87F98bc399FD19f86AfFAE85D80e150CE92F170') // 
        const artBlocksHistory = await getArtBlocksUserHistory(artBlockEvents)

        const NFTEvents = await getNFTEvents(wallet.signerAddress) //await getNFTEvents('0xc87F98bc399FD19f86AfFAE85D80e150CE92F170')// 
        const NFTHistory = await getNFTHistory(NFTEvents)

        let allUserHistory = []

        // Add Art Blocks history
        if (artBlocksHistory.length > 0) {
          allUserHistory.push({
            'name': 'Art Blocks',
            'events': artBlocksHistory.map((history, item) => { 
              return { 
                projectId: history.artBlocksProjectId.toString(), 
                quantity: history.quantity.toString(),
                bid: history.priceInWeiEach.toString() / 1e18, 
                action: history.action,
                hash: history.txHash
              }
            }).reverse()
          })
        }

        // Add general NFT history
        let NFTEventTokenAddressess = [...new Set(NFTHistory.map(event => event.tokenAddress))]
        NFTEventTokenAddressess.forEach(tokenAddress => {
          allUserHistory.push({
            'name': NFTAddresses.filter(address => address.contract === tokenAddress)[0].name,
            'events': NFTHistory.filter(event => event.tokenAddress === tokenAddress).map(event => {
              return {
                projectId: false,
                quantity: event.quantity.toString(),
                bid: event.priceInWeiEach.toString() / 1e18, 
                action: event.action,
                hash: event.txHash
              }
            }).reverse()
          })
        })

        allUserHistory.sort((group1, group2) => group1.name.toUpperCase() < group2.name.toUpperCase() ? -1 : 1)

        setUserHistory(prevState => allUserHistory)
        setUserHistoryState(prevState => '')
      }
    } catch(err) {
      console.log(err)
      setUserHistoryState(prevState => 'error')
    }
  }

  async function cancelOrder(id, type, quantity, bid) {

    function updateOrderStatus(newStatus) {
      setUserOrders(prevUserOrders => {
        return prevUserOrders.map(order => {
          if (order.id === id) {
            order.status = newStatus
          }
          return order
        })
      })
    }

    try {

      updateOrderStatus('pending')

      let cancelBidResponse
      if (type === 'Art Blocks') {
        const artBotterContract = new ethers.Contract(artBotBrokerAddress, artBotBrokerAbi, wallet.signer)
        cancelBidResponse = await artBotterContract.cancelOrder(id)
      } else {
        const generalContract = new ethers.Contract(ERC721LimitOrderAddress, ERC721LimitOrderAbi, wallet.signer)
        cancelBidResponse = await generalContract.cancelOrder(id)
      }

      // Add the cancel tx to the Pending Txs notifications
      const contractName = (type === 'Art Blocks') ? `Art Blocks #${id}` : NFTAddresses.filter(address => address.contract === id)[0].name
      addPendingTx(contractName, quantity, bid, cancelBidResponse.hash, 'Canceled')

      // Change the "cancel" text to "pending cancel"
      setUserOrders(prevUserOrders => {
        return prevUserOrders.map(order => {
          if (order.id === id) {
            order.status = 'txPending'
            order.text = '(cancel pending)'
          }
          return order
        })
      })

      // Update the UI when the cancelOrder transaction succeeds or fails
      try {
        const cancelResponseUpdate = await cancelBidResponse.wait()
        const responseTxHash = cancelResponseUpdate.transactionHash

        // Update the pendingTx status to "confirmed"
        updatePendingTxStatus(responseTxHash, 'confirmed')

        // Remove order from list of user orders
        setUserOrders(prevUserOrders => prevUserOrders.filter(order => order.id !== id))

      } catch(err) {
        console.log(err)
        const txHashRegex = /transactionHash="([a-z0-9]*)"/gi
        const txHashMatch = txHashRegex.exec(err.message)
        const responseTxHash = txHashMatch[1]
        
        // Update the pendingTx status to "failed"
        updatePendingTxStatus(responseTxHash, 'failed')
        updateOrderStatus('placed')
      }

    } catch(err) {
      updateOrderStatus('placed')
    }

  }

  useEffect(() => {
    if (wallet.connected) {
      getUserOrders()
    }
  }, [wallet])

  useEffect(() => {
    if (wallet.connected) {
      getUserHistory()
    }
  }, [wallet])

  useEffect(() => {
    if (wallet.connected) {
      window.setTimeout(() => { 
        getUserHistory()
      }, 4000)
    }
  }, [userOrders.length])

  return (
    <ThemeProvider theme={BaseTheme}>
      <CssBaseline />
      <div className="Header">
        <h1>ArtBotter</h1>
        <div>
          { wallet.connected ?
              <div className="Header__connected">
                <span><CheckCircleIcon color="secondary" style={{ marginRight: '0.5rem' }} /> Wallet Connected</span>
                <span>{ wallet.signerAddress.substr(0,8) + '...' + wallet.signerAddress.substr(-5) }</span>
              </div>
            :
              <div>
                <Connect 
                  wallet={wallet} 
                  setWallet={setWallet} 
                  metamaskInstalled={metamaskInstalled} 
                  setMetamaskInstalled={setMetamaskInstalled} />
              </div>
          }
        </div>
      </div>
      <Container className="Page" maxWidth="sm">
        <div className="Body">

          <div className="Body__section Body__about">
            <h2>About ArtBotter</h2>
            <p>ArtBotter makes purchase-via-bot accessible to all NFT buyers so you have the best chance possible to purchase the art you love.</p>
            <p>Place a bid below on an NFT project before it drops and bots will compete to mint an NFT for you as soon as they become available at your designated price. If the drop sells out before your order is filled, simply cancel your bid for a full refund.</p>
            <p>ArtBotter is a trustless marketplace: When you place a bid, your money is held in escrow and can be retrieved at any time by canceling the bid before it's filled. Your money is only sent to a bot when they have successfully filled your order.</p>
            <p>Bots attempt to fill the highest bids first. See the question "How much should I offer to pay?" below for some thoughts on what to bid.</p>
          </div>

          <div className="Body__section Body__order">
            <div>
              <div className="Step__action">
                <h3>Place a Bid</h3>
                <p>Choose an NFT project, the maximum number of NFTs you want to purchase, and the amount of ETH you're offering to pay for each of them. Don't see the project you want? Ask in <a href="https://discord.gg/yUqAB575vE" target="_blank" rel="noreferrer">our Discord</a>.</p>
                <BidForm 
                  artBotBrokerAddress={artBotBrokerAddress} 
                  artBotBrokerAbi={artBotBrokerAbi} 
                  ERC721LimitOrderAddress={ERC721LimitOrderAddress}
                  ERC721LimitOrderAbi={ERC721LimitOrderAbi}
                  wallet={wallet} 
                  NFTAddresses={NFTAddresses}
                  bidFields={bidFields} 
                  setBidFields={setBidFields}
                  pendingTxs={pendingTxs}
                  setPendingTxs={setPendingTxs}
                  addPendingTx={addPendingTx}
                  updatePendingTxStatus={updatePendingTxStatus}
                  setUserOrders={setUserOrders}
                />
              </div>
            </div>
          </div>

          <div className="Body__section Body__open-orders">
            <div>
              <div className="Step__action">
                <h3>Your Open Bids</h3>
                { !wallet.connected && 
                  <div className="Section__not-connected">
                    <p>Connect your wallet to see your open bids.</p>
                  </div>
                }
                { wallet.connected && userOrderState === 'pending' &&
                  <div className="Section__pending">
                    <Spinner />
                  </div>
                }
                { wallet.connected && userOrderState === 'error' &&
                  <div className="Section__error">
                    <p>Sorry, something went wrong. Please refresh to attempt fetching orders again.</p>
                  </div>
                }
                { wallet.connected && userOrderState === '' && userOrders.length === 0 &&
                  <div className="Section__none">
                    <p>You have no open bids.</p>
                  </div>
                }
                { wallet.connected && userOrderState === '' && userOrders.length > 0 &&
                  <ul className="Section__list">
                    { userOrders.map((order, index) => {
                        return <li key={index} className={order.status !== 'placed' ? 'disabled' : ''}>
                                <b>{order.name}: </b> {order.quantity} NFT{parseInt(order.quantity) === 1 ? '' : 's'} for {order.bid} ETH{order.quantity > 1 ? ' each' : ''}. 
                                <span onClick={() => cancelOrder(order.id, order.type, order.quantity, order.bid)}>
                                  { order.status === 'pending' ?
                                    <Spinner />
                                    :
                                    order.text
                                  }
                                </span>
                              </li>
                      })
                    }
                  </ul>
                }
              </div>
            </div>
          </div>

          <div className="Body__section Body__user-history">
            <div>
              <div className="Step__action">
                <h3>Your Bid History</h3>
                { !wallet.connected && 
                  <div className="Section__not-connected">
                    <p>Connect your wallet to see your history.</p>
                  </div>
                }
                { wallet.connected && userHistoryState === 'pending' &&
                  <div className="Section__pending">
                    <Spinner />
                  </div>
                }
                { wallet.connected && userHistoryState === 'error' &&
                  <div className="Section__error">
                    <p>Sorry, something went wrong. Please refresh to attempt fetching history again.</p>
                  </div>
                }
                { wallet.connected && userHistoryState === '' && userHistory.length === 0 &&
                  <div className="Section__none">
                    <p>You currently have no user history.</p>
                  </div>
                }
                { wallet.connected && userHistoryState === '' && userHistory.length > 0 &&
                  <div className="Section__list-container">
                    { userHistory.map((group, i) => {
                        return (
                          <div key={i} className="Section__sublist-container">
                            <h4>{group.name}</h4>
                            <ul className="Section__sublist">
                              { group.events.map((event, j) => {
                                const projectName = event.projectId ? `#${event.projectId}` : group.name
                                return (
                                  <li key={j}>
                                    { event.action === 'order placed' &&
                                      <Fragment>
                                        <b>Placed: </b>{event.quantity} NFT{event.quantity === '1' ? '' : 's'} from { projectName } for {event.bid} ETH{event.quantity > 1 ? ' each' : ''}.
                                      </Fragment>
                                    }
                                    { event.action === 'order cancelled' &&
                                      <Fragment>
                                        <b>Cancelled: </b>Your bid for { projectName }.
                                      </Fragment>
                                    }
                                    { event.action === 'order fulfilled' && event.quantity > '0' &&
                                      <Fragment>
                                        <b>Bid Partially Filled: </b>1 NFT from { projectName } for {event.bid} ETH each.
                                      </Fragment>
                                    }
                                    { event.action === 'order fulfilled' && event.quantity === '0' &&
                                      <Fragment>
                                        <b>Bid Completely Filled: </b>1 NFT from { projectName }.
                                      </Fragment>
                                    }
                                    <span><a href={`https://etherscan.io/tx/${event.hash}`} target="_blank" rel="noreferrer">tx link</a></span>
                                  </li>
                                )
                              } )}
                            </ul>
                          </div>
                        )
                      })
                    }
                  </div>
                }
              </div>
            </div>
          </div>

          {/*<div className="Body__section Body__all-open-orders">
            <div>
              <div className="Step__action">
                <h3>See Your Competition</h3>
                <p>Enter an Art Blocks project ID to see a list of all open ArtBotter orders for that project.</p>
                <ul className="AllOpenOrders__list">
                  <li><span>1. </span>1.4000 ETH</li>
                  <li><span>2. </span>1.3500 ETH</li>
                  <li><span>3. </span>0.8555 ETH</li>
                  <li><span>4. </span>0.8554 ETH</li>
                  <li><span>5. </span>0.8000 ETH</li>
                  <li><span>6. </span>0.8000 ETH</li>
                  <li><span>7. </span>0.8000 ETH</li>
                  <li><span>8. </span>0.8000 ETH</li>
                  <li><span>9. </span>0.8000 ETH</li>
                  <li><span>10. </span>0.7500 ETH</li>
                  <li><span>11. </span>0.7000 ETH</li>
                  <li><span>12. </span>0.7000 ETH</li>
                  <li><span>13. </span>0.7000 ETH</li>
                  <li><span>14. </span>0.7000 ETH</li>
                </ul>
              </div>
            </div>
          </div>*/}

          <div className="PendingTxs">
            { 
              pendingTxs.map((tx, index) => {
                return <PendingTx tx={tx} key={index} />  
              })
            }
          </div>

          <div className="Body__section Body__faq">
            <h2>Frequently Asked Questions</h2>
            <dl className="FAQ__list">
              <dt>Have the ArtBotter smart contracts been audited?</dt>
              <dd>No. The code is open source and anyone is free to check it out <a href="https://etherscan.io/address/0xE52A2A7B8dACe2ff8cdC64EabB605821A393474D#code">here</a> (ArtBlocks version) or <a href="https://etherscan.io/address/0x3c0187a23ec0889b43848086f0be1e5033d10d96#code">here</a> (general ERC721 version). It is straightforward to analyze, but the code has not been audited, and you should not send it more money than you'd be happy losing. If you lose money, there is nothing we can do for you. You won't be reimbursed in any way. We'll be trusting the contract with our own money, but this is alpha software and you should treat it as such.</dd>

              <dt>How much should I offer to pay?</dt>
              <dd>
                <p>That's up to you! But here are some things to keep in mind.</p>
                <p>Bots won't try to fill your order unless your bid covers their costs. Bots have to use more gas than normal users, because they need to mint the NFT <i>and</i> transfer the NFT to you. Bots have to participate in any gas wars that occur during the drop, and bots pay ArtBotter a 1% fee for our services.</p>
                <p>For highly-hyped, fixed-price drops it is not uncommon to see bids of more than 1 ETH above the minting price. For dutch auction drops, bids of your target minting price + 20% may do fine. Your guess is as good as ours when it comes to predicting what gas prices will be during a drop.</p>
                <p>ArtBotter won't help you buy the NFTs more cheaply than you could mint them on your own. Instead, it will:</p>
                  <p>- Help you be faster so you are more likely to get an NFT before they sell out. Bots can adjust gas prices and resubmit transactions faster than any user could do manually.</p>
                  <p>- Ensures you don't ever lose money on reverting transactions. So you can bid as high as you would <i>as if</i> you were gas warring by yourself, but without the risk of losing money on reverting transactions.</p>
                  <p>- Lets you bypass the project's UI. Bots can start minting and filling your orders before a project's UI is updated to start allowing purchases.</p>
                  <p>- Bypass any per-account mint limits an NFT project may have.</p>
              </dd>

              <dt>If bots spend less than my bid amount to fill my order, do I get the remainder back?</dt>
              <dd>No. The difference between your bid and what a bot spent to fill your order is the profit for the bot that filled your order. This is what incentivizes the bots to compete to fill your orders.</dd>

              <dt>Can I submit bids for more than one NFT per project?</dt>
              <dd>Yes. With ArtBotter you can ask for as many NFTs as you want -- even if the NFT project has a per-account mint limit.</dd>

              <dt>Can I submit bids for multiple projects at once?</dt>
              <dd>Yes. You can place bids for as many projects as you want.</dd>

              <dt>What happens if I bid on an NFT but don't win it?</dt>
              <dd>Simply cancel your bid for a full refund. No problem.</dd>

              <dt>Can my bid be partially filled?</dt>
              <dd>Yes. For example, suppose you place a bid for 10 NFTs offering to pay 1 ETH each, so you've deposited 10 ETH into ArtBotter. It is possible the bots will only manage to get 7 of the NFTs for you. You could then cancel the remainder of your bid (3 NFTs) and get the remainder of your deposit (3 ETH) back.</dd>

              <dt>How do I increase or decrease my bid after placing it?</dt>
              <dd>To increase or decrease your bid you must first cancel your existing bid, and then place a new bid.</dd>

              <dt>Are there any fees?</dt>
              <dd>Bots that fill orders through ArtBotter pay a fee of 1% to ArtBotter. Users pay no fees at all.</dd>

              <dt>Can I specify an individual tokenId that I want?</dt>
              <dd>No. When you place a bid for a NFT, you specify a particular project, and your bid can be filled with any NFT from that project. You may end up with "floor price" NFTs or rare NFTs. You never know.</dd>

              <dt>Does ArtBotter work if the project uses a Dutch auction?</dt>
              <dd>Yes. ArtBotter works with any possible initial distribution mechanism, including auctions. The smart contract is completely agnostic to the initial distribution method and any restrictions on them (e.g. limited minting requirements, no-contract purchases, etc). ArtBotter users never need to worry about any of that. The bots will handle it. You make a large enough offer for some NFTs, and the bots will do whatever it takes to get them for you.</dd>

              <dt>Can I order an NFT that requires something other than ETH to mint?</dt>
              <dd>Yes, ArtBotter can fill orders for NFTs that require something other than ETH to mint. However, when you create an order on ArtBotter, you must make your offer in ETH.</dd>

              <dt>How do I get in touch with someone about ArtBotter?</dt>
              <dd>Feel free to join <a href="https://discord.gg/yUqAB575vE" target="_blank" rel="noreferrer">our Discord</a>.</dd>
            </dl>
          </div>

        </div>
      </Container>
    </ThemeProvider>
  )
}
