import { useState } from 'react'
import { ethers } from 'ethers'
import _ from 'lodash'

import { Button, FormHelperText, InputAdornment, InputLabel, MenuItem, Select, TextField, Tooltip } from '@material-ui/core'
import HelpIcon from '@material-ui/icons/Help'

import Spinner from './../Spinner'

import { getProjectDetails } from './../../scripts/helpers'

export default function BidForm(props) {
  const [ projectDetails, setProjectDetails ] = useState({
    name: '',
    author: ''
  })

  const { artBotBrokerAddress, 
          artBotBrokerAbi, 
          ERC721LimitOrderAddress,
          ERC721LimitOrderAbi,
          wallet, 
          NFTAddresses,
          bidFields, 
          setBidFields, 
          addPendingTx, 
          updatePendingTxStatus, 
          setUserOrders 
        } = props

  const retrieveProjectInfo = async (e) => {
    if (e.target.value === '') {
      setProjectDetails(prevProjectDetails => {
        return { ...prevProjectDetails, name: '', author: '' }
      })
    } else {
      const projectInfo = await getProjectDetails(e.target.value)
      setProjectDetails(prevProjectDetails => {
        return { ...prevProjectDetails, name: projectInfo.projectName, author: projectInfo.artist }
      })
    }
  }

  const onProjectSelect = (e) => {
    const selection = e.target.value
    const correspondingAddress = NFTAddresses.filter(item => item.name === selection)[0]
    const projectIdRequired = correspondingAddress.name === 'Art Blocks' ? true : false
    setBidFields(prevBidFields => {
      return { 
        ...bidFields, 
        projectSelect: selection, 
        projectSelectError: '', 
        contractAddress: correspondingAddress.contract, 
        projectId: '', 
        projectIdRequired, 
        contractAddressError: '', 
        projectIdError: '', 
        bidError: '', 
        generalError: ''  
      }
    })
    setProjectDetails(prevProjectDetails => {
      return { ...prevProjectDetails, name: '', author: '' }
    })
  }

  // Handle the bid form submission
  const handleSubmitBid = async () => {

    const projectId = bidFields.projectId,
          quantity = bidFields.quantity,
          bid = parseFloat(bidFields.bid),
          contractAddress = bidFields.contractAddress,
          isArtblocks = bidFields.projectSelect === 'Art Blocks' ? true : false,
          contractName = isArtblocks ? `Art Blocks #${projectId}` : bidFields.projectSelect

    setBidFields(prevBidFields => {
      return { ...prevBidFields, pending: true, projectSelectError: '', contractAddressError: '', projectIdError: '', bidError: '', generalError: '' }
    })

    // Error checking
    if (!wallet.connected || _.isEmpty(wallet.signer)) {
      setBidFields(prevBidFields => {
        return { ...prevBidFields, pending: false, generalError: 'Please connect your wallet.' }
      })
      return
    }
    // Error checking: NFT Address select field
    if (typeof bidFields.projectSelect !== 'string' || bidFields.projectSelect.length < 1) {
      setBidFields(prevBidFields => {
        return { ...prevBidFields, pending: false, projectSelectError: 'Please select an NFT project.' }
      })
      return
    }
    // Error checking: Smart Contract Address
    if (typeof contractAddress !== 'string' || contractAddress.length < 1) {
      setBidFields(prevBidFields => {
        return { ...prevBidFields, pending: false, contractAddressError: 'Please enter a smart contract address.' }
      })
      return
    }
    if (!contractAddress.startsWith('0x')) {
      setBidFields(prevBidFields => {
        return { ...prevBidFields, pending: false, contractAddressError: 'Invalid smart contract address (does not start with "0x").' }
      })
      return
    }
    if (/[^0-9a-z]/gi.test(contractAddress)) {
      setBidFields(prevBidFields => {
        return { ...prevBidFields, pending: false, contractAddressError: 'Please enter a valid smart contract address.' }
      })
      return
    }
    // Error checking: Art Blocks Project ID
    // Only throw an error if project ID is required
    if (bidFields.projectIdRequired && (isNaN(parseInt(projectId)) || typeof parseInt(projectId) !== 'number' || projectId < 0)) {
      setBidFields(prevBidFields => {
        return { ...prevBidFields, pending: false, projectIdError: 'Please enter the ID of an existing ArtBlocks project.' }
      })
      return
    }
    // Error checking: Quantity
    if (isNaN(parseInt(quantity)) || typeof parseInt(quantity) !== 'number' || quantity < 0) {
      setBidFields(prevBidFields => {
        return { ...prevBidFields, pending: false, quantityError: 'Please enter a valid quantity.' }
      })
      return
    }
    // Error checking: Bid
    if (isNaN(bid) || typeof bid !== 'number') {
      setBidFields(prevBidFields => {
        return { ...prevBidFields, pending: false, bidError: 'Please enter a valid bid.' }
      })
      return
    }
    if (bid <= 0) {
      setBidFields(prevBidFields => {
        return { ...prevBidFields, pending: false, bidError: 'You must bid more than 0 ETH.' }
      })
      return
    }

    try {
      // Submit bid transaction to the blockchain
      const totalBid = (bid * parseInt(quantity)).toFixed(4).toString()

      let setBidResponse
      if (isArtblocks) {
        const artBotterContract = new ethers.Contract(artBotBrokerAddress, artBotBrokerAbi, wallet.signer)
        setBidResponse = await artBotterContract.placeOrder(projectId, quantity, { value: ethers.utils.parseEther(totalBid) })        
      } else {
        const generalNFTContract = new ethers.Contract(ERC721LimitOrderAddress, ERC721LimitOrderAbi, wallet.signer)
        setBidResponse = await generalNFTContract.placeOrder(contractAddress, quantity, { value: ethers.utils.parseEther(totalBid) })        
      }

      // Show submission success
      setBidFields(prevBidFields => {
        return { 
          ...prevBidFields, 
          pending: false, 
          button: 'Submitted!' 
        }
      })
      window.setTimeout(() => { 
        setBidFields(prevBidFields => {
          return { ...prevBidFields, button: 'Place Bid' }
        })
      }, 2000)


      // Update state with pending transaction information
      addPendingTx(contractName, quantity, bid, setBidResponse.hash, 'Placed')

      window.setTimeout(() => { 
        setBidFields(prevBidFields => {
          return { ...prevBidFields, button: 'Place Bid' }
        })
      }, 2000)

      // Update the UI when the pendingBid transaction succeeds or fails
      try {
        const bidResponseUpdate = await setBidResponse.wait()
        const responseTxHash = bidResponseUpdate.transactionHash

        // Update the pendingTx status to "confirmed"
        updatePendingTxStatus(responseTxHash, 'confirmed')

        // Add the order to the list of user orders
        setUserOrders(prevUserOrders => {
          const newUserOrders = [...prevUserOrders]
          newUserOrders.push({ 
            name: contractName,
            id: isArtblocks ? projectId : contractAddress,
            type: isArtblocks ? 'Art Blocks' : 'General',
            quantity, 
            bid, 
            status: 'placed',
            text: 'cancel' 
          })

          const sortedOrders = newUserOrders.sort((first, second) => (first.projectId > second.projectId) ? -1 : 1)
          return sortedOrders
        })

      } catch(err) {
        console.log(err)
        const txHashRegex = /transactionHash="([a-z0-9]*)"/gi
        const txHashMatch = txHashRegex.exec(err.message)
        const responseTxHash = txHashMatch[1]
        
        // Update the pendingTx status to "failed"
        updatePendingTxStatus(responseTxHash, 'failed')
      }

    } catch(err) {

      console.log(err.code)

      let errorMessage = ''
      //console.log(err.code, err.data)
      switch (err.code) {
        // Error: user rejects transaction
        // MetaMask - RPC Error: MetaMask Tx Signature: User denied transaction signature. {code: 4001, message: "MetaMask Tx Signature: User denied transaction signature."}
        case 4001:
          errorMessage = 'User denied transaction signature.'
          break
        // Error: user has insufficient funds
        // MetaMask - RPC Error: err: insufficient funds for gas * price + value. {code: -32000, message: "err: insufficient funds for gas * price + value: awant 12000000000000000000 (supplied gas 15054442)"}
        case 'INSUFFICIENT_FUNDS':
          errorMessage = 'Insufficient funds.'
          break
        // Contract errors
        case 'UNPREDICTABLE_GAS_LIMIT':
          errorMessage = err.error.message.replace('execution reverted: ', '').replace('invalid AB id', 'Please enter a valid ArtBlocks project ID.')
          break
        default:
          errorMessage = 'Sorry, something went wrong. Please try again.'
      }

      setBidFields(prevBidFields => {
        return { ...prevBidFields, pending: false, generalError: errorMessage }
      })
    }
  }

  return (
    <form className="OrderForm" noValidate autoComplete="off">
      <div className="OrderForm__fields">
        <div>
          <InputLabel>NFT Project</InputLabel>
          <Select
            id="nft-project-select"
            className="OrderForm__NFTProjectSelect"
            value={bidFields.projectSelect}
            error={bidFields.projectSelectError.length > 0}
            disabled={bidFields.pending}
            onChange={onProjectSelect}
          >
            { NFTAddresses.filter(address => address.active === true).map((address, index) => {
              return <MenuItem value={address.name} key={index}>{address.name}</MenuItem>
            })}
          </Select>
          { bidFields.projectSelectError.length > 0 &&
            <FormHelperText className="Mui-error">{bidFields.projectSelectError}</FormHelperText>
          }
        </div>
        { bidFields.projectSelect === 'Other' &&
          <div>
            <InputLabel>
              NFT Smart Contract Address 
              <Tooltip style={{ marginLeft: '0.25rem' }} title={<p style={{ margin: 0 }}>Enter the smart contract address used by the NFT project that you want to bid on. You can usually find the contract address somewhere on the project's website or by asking in their Discord.</p>}>
                  <HelpIcon fontSize="small" />
              </Tooltip>
            </InputLabel>
            <TextField 
              id="smart-contract-address" 
              variant="standard"
              placeholder="ex: 0xa7d8d9ef8d8ce8992df33d8b8cf4aebabd5bd270"
              value={bidFields.contractAddress}
              error={bidFields.contractAddressError.length > 0}
              helperText={bidFields.contractAddressError}
              disabled={bidFields.pending}
              onChange={(e) => { setBidFields({ ...bidFields, contractAddress: e.target.value, contractAddressError: '' }) }}
            />
          </div>
        }
        { bidFields.projectSelect === 'Art Blocks' &&
          <div>
            <InputLabel>
              Art Blocks Project ID 
              <Tooltip style={{ marginLeft: '0.25rem' }} title={<p style={{ margin: 0 }}>Enter the project ID number associated with the ArtBlocks project that you'd like to bid on. You can find the ID in the url of the project's page on the ArtBlocks website (ex: artblocks.io/project/116).</p>}>
                  <HelpIcon fontSize="small" />
              </Tooltip>
            </InputLabel>
            <TextField 
              id="artblocks-project-id" 
              variant="standard"
              placeholder="ex: 116"
              value={bidFields.projectId}
              error={bidFields.projectIdError.length > 0}
              helperText={bidFields.projectIdError}
              onBlur={retrieveProjectInfo}
              disabled={bidFields.pending}
              onChange={(e) => {
                // Make sure only integers are allowed
                // Note: We are not using the "number" field type because
                // the user was able to enter "-", and when the user did so
                // the onChange event would not fire
                const value = (e.target.value).replace(/[^0-9]/gi, '')

                // Limit the entered text to zero decimals
                const decimalCount = (value.split('.')[1] || '').length
                const projectId = decimalCount < 1 ? value : parseFloat(value).toFixed(0)
                setBidFields({ ...bidFields, projectId, projectIdError: '', generalError: '' })
              }}
            />
          </div>
        }
        <div>
          <InputLabel>
            Quantity
            <Tooltip style={{ marginLeft: '0.25rem' }} title={<p style={{ margin: 0 }}>Enter the maximum quantity of NFTs you'd like to purchase from this project.</p>}>
                <HelpIcon fontSize="small" />
            </Tooltip>
          </InputLabel>
          <TextField 
            id="price-to-offer" 
            variant="standard"
            placeholder="ex: 4"
            value={bidFields.quantity}
            error={bidFields.quantityError.length > 0}
            helperText={bidFields.quantityError}
            disabled={bidFields.pending}
            onChange={(e) => {
              // Make sure only numbers and decimals are allowed
              // Note: We are not using the "number" field type because
              // the user was able to enter "-", and when the user did so
              // the onChange event would not fire
              const value = (e.target.value).replace(/[^0-9]/gi, '')

              // Limit the entered text to zero decimals
              const decimalCount = (value.split('.')[1] || '').length
              const quantity = decimalCount < 5 ? value : parseFloat(value).toFixed(4)
              setBidFields({ ...bidFields, quantity, quantityError: '', generalError: '' })
            }}
          />
        </div>
        <div>
          <InputLabel>
            Bid per NFT
            <Tooltip style={{ marginLeft: '0.25rem' }} title={<p style={{ margin: 0 }}>Enter the amount you want to bid per individual NFT. This amount needs to be at least the minimum price set by the project plus the transaction fee you are willing to pay. Higher bids will be filled first.</p>}>
                <HelpIcon fontSize="small" />
            </Tooltip>
          </InputLabel>
          <TextField 
            id="price-to-offer" 
            variant="standard"
            placeholder="ex: 3.1250"
            value={bidFields.bid}
            error={bidFields.bidError.length > 0}
            helperText={bidFields.bidError}
            disabled={bidFields.pending}
            onChange={(e) => {
              // Make sure only numbers and decimals are allowed
              // Note: We are not using the "number" field type because
              // the user was able to enter "-", and when the user did so
              // the onChange event would not fire
              const value = (e.target.value).replace(/[^0-9.]/gi, '')

              // Limit the entered text to four decimals
              const decimalCount = (value.split('.')[1] || '').length
              const bid = decimalCount < 5 ? value : parseFloat(value).toFixed(4)
              setBidFields({ ...bidFields, bid, bidError: '', generalError: '' })
            }}
            InputProps={{
              endAdornment: <InputAdornment position="end">ETH</InputAdornment>,
            }}
          />
        </div>
      </div>
      { bidFields.projectSelect !== '' &&
        <p className="OrderForm__fields-description">You are offering to buy up to <b>{bidFields.quantity || 0} <a href={bidFields.projectSelect === 'Art Blocks' ? `https://artblocks.io/project/${bidFields.projectId}` : NFTAddresses.filter(address => address.name === bidFields.projectSelect)[0].url} target="_blank" rel="noreferrer">{bidFields.projectSelect === 'Art Blocks' ? projectDetails.name : bidFields.projectSelect} NFT{parseInt(bidFields.quantity) === 1 ? '' : 's'}</a></b> {projectDetails.author && `by ${projectDetails.author}`} for <b>{bidFields.bid || 0} ETH</b> each. Your total cost is <b>{(bidFields.bid > 0 && bidFields.quantity > 0) ? ((parseFloat(bidFields.bid) * parseInt(bidFields.quantity))).toFixed(4).toString() : '0'} ETH</b>.</p>
      }
      { bidFields.generalError &&
        <p className="OrderForm__general-error">{bidFields.generalError}</p>
      }
      <div className="OrderForm__submit">
        <Button variant="contained" color="primary" disableElevation onClick={handleSubmitBid} disabled={bidFields.projectSelectError.length > 0 || bidFields.projectIdError.length > 0 || bidFields.quantityError.length > 0 || bidFields.bidError.length > 0}>
          { bidFields.pending ? 
              <Spinner />
            :
              bidFields.button
          }
        </Button>
      </div>
    </form>
  )
}
