const ethers = require('ethers')
// const provider = new ethers.providers.JsonRpcProvider('http://127.0.0.1:8545')
// const provider = new ethers.providers.JsonRpcProvider('https://eth-mainnet.alchemyapi.io/v2/FbqRWPO255vt3CfeI0cKz5qcYuDfUsCb')
const provider = new ethers.providers.JsonRpcProvider('https://mainnet.infura.io/v3/522e1f990a394210a80a844461d3e814')

export const ERC721LimitOrderAddress = '0x3C0187a23ec0889b43848086F0Be1e5033d10D96'
const ERC721LimitOrderAbi = require('../abis/ERC721LimitOrder')
const ERC721LimitOrderInterface = new ethers.utils.Interface(ERC721LimitOrderAbi)
const ERC721LimitOrderContract = new ethers.Contract(ERC721LimitOrderAddress, ERC721LimitOrderAbi, provider)

const filterStartBlock = 10000000;

export const getEventsForUser = async function(user) {
	// get all events related to this user
	const filter = ERC721LimitOrderContract.filters.Action(user)
	filter.fromBlock = filterStartBlock
	const logs = await provider.getLogs(filter)
	// console.log(logs)
	const events = logs.map(log => {
		let parsedLog = ERC721LimitOrderInterface.parseLog(log)
		parsedLog.txHash = log.transactionHash
		return parsedLog
	})
	return events
}

export const getEventsForTokenAddress = async function(tokenAddress) {
	// get all events related to this tokenAddress
	const filter = ERC721LimitOrderContract.filters.Action(null, tokenAddress)
	filter.fromBlock = filterStartBlock
	const logs = await provider.getLogs(filter)
	// console.log(logs)
	const events = logs.map(log => {
		let parsedLog = ERC721LimitOrderInterface.parseLog(log)
		parsedLog.txHash = log.transactionHash
		return parsedLog
	})
	return events
}

const getAllERC721LimitOrderEvents = async function() {
	// get all events from the ERC721LimitOrder contract
	const filter = ERC721LimitOrderContract.filters.Action()
	filter.fromBlock = filterStartBlock
	const logs = await provider.getLogs(filter)
	// console.log(logs)
	const events = logs.map(log => {
		let parsedLog = ERC721LimitOrderInterface.parseLog(log)
		parsedLog.txHash = log.transactionHash
		return parsedLog
	})
	return events
}

// use the `getEventsForUser` function to get eventsForUser 
export const getLatestOrdersForUser = function(user, eventsForUser) {
	// get the set of all ERC721's this user has ever created an order for
	const tokenAddresses = Array.from(new Set(eventsForUser.map(event => event.args.tokenAddress.toString())))
	// for each token address, get the user's latest order
	const orders = tokenAddresses.map(tokenAddress => {
		// get all the logs for this user related to the this project ID
		const eventsForThisUserAndTokenAddress = eventsForUser.filter(event => event.args.tokenAddress === tokenAddress)
		if (eventsForThisUserAndTokenAddress.length === []) throw new Error(`eventsForThisUserAndTokenAddress is empty when it shouldn't be. user: ${user}, tokenAddress: ${tokenAddress}`)
		// logs are chronological, so just grab the latest one
		const latestEvent = eventsForThisUserAndTokenAddress[eventsForThisUserAndTokenAddress.length - 1]
		return {tokenAddress: latestEvent.args.tokenAddress, priceInWeiEach: latestEvent.args.priceInWeiEach, quantity: latestEvent.args.quantity}
	})
	// filter out those orders that have 0 _quantity
	const nonEmptyOrders = orders.filter(order => !order.quantity.eq('0'))

	return nonEmptyOrders
}

// use the `getEventsForTokenAddress` function to get eventsForTokenAddress 
const getLatestOrdersForTokenAddress = function (eventsForTokenAddress) {
	const eventsInReversedOrder = eventsForTokenAddress.reverse()
	// go through all the events (in reverse order), grab the latest user order and store it in a Map
	const ordersMap = new Map()
	eventsInReversedOrder.map(event => {
		if (!ordersMap.has(event.args._user)) {
			ordersMap.set(event.args.user, {user: event.args.user, priceInWeiEach: event.args.priceInWeiEach, quantity: event.args.quantity, readableQuantity: event.args.quantity.toString(), priceInEthEach: ethers.utils.formatEther(event.args.priceInWeiEach)})
		}
	})
	const ordersArray = Array.from(ordersMap, ([, value]) => value)
	const nonZeroOrders = ordersArray.filter(order => !order.quantity.eq('0'))
	return nonZeroOrders
}

const getEventsForTokenAddressFromAllEvents = function(tokenAddress, allEvents) {
	return allEvents.filter(event => event.args.tokenAddress === tokenAddress)
}

// returns a Map(tokenAddress => array of orders sorted from highest to lowest price offered)
const getSortedOrdersForAllTokenAddresses = async function() {
	const allEvents = await getAllERC721LimitOrderEvents()
	const tokenAddresses = Array.from(new Set(allEvents.map(event => event.args.tokenAddress.toString())))
	const orders = new Map()
	tokenAddresses.map(tokenAddress => {
		const eventsForTokenAddress = getEventsForTokenAddressFromAllEvents(tokenAddress, allEvents)
		const latestOrders = getLatestOrdersForTokenAddress(eventsForTokenAddress)
		// console.log('latestOrders')
		// console.log(latestOrders)
		// sort latestOrders based on price offered
		const sortedOrders = latestOrders.sort((a,b) => a.priceInWeiEach.gt(b.priceInWeiEach))
		orders.set(tokenAddress, sortedOrders)
	})
	return orders
}

const getTokenDetails = async function(tokenAddress) {
	// create the ethersjs contract object
	const ERC721Abi = ["function name() view returns (string)", "function symbol() view returns (string)"]
	const ERC721Contract = new ethers.Contract(tokenAddress, ERC721Abi, provider)

	// get name
	let name;
	try {
		name = await ERC721Contract.name()
	} catch {
		name = 'Unknown Name'
	}

	// get symbol
	let symbol;

	try {
		symbol = await ERC721Contract.symbol()
	} catch {
		symbol = 'Unknown Symbol'
	}

	// return name and symbol
	return {name: name, symbol: symbol}
}

// use the `getEventsForUser` function to get eventsForUser 
export const getUserHistory = async function(eventsForUser) {
	// get all events related to this user
	const historyArray = eventsForUser.map(event => {
		const action = {
			action: event.args.action,
			tokenAddress: event.args.tokenAddress,
			optionalTokenId: event.args.optionalTokenId,
			priceInWeiEach: event.args.priceInWeiEach,
			quantity: event.args.quantity,
			txHash: event.txHash
		}
		return action
	})
	return historyArray
}

// use the `getEventsForUser` function to get eventsForUser 
const getHumanReadableUserHistory = async function(eventsForUser) {
	const userHistory = await getUserHistory(eventsForUser)
	const humanReadableUserHistory = [];
	for (let i = 0; i < userHistory.length; i++) {
		humanReadableUserHistory.push(await stringifyUserAction(userHistory[i]))
	}
	return humanReadableUserHistory
}

async function stringifyUserAction(event) {
	const projectDetails = await getTokenDetails(event.tokenAddress)
	if (event.action === 'order placed') {
		return {
			explanation: `Order placed for token: ${projectDetails.name} (${projectDetails.symbol}). Quantity: ${event.quantity}. Price offered each: ${ethers.utils.formatEther(event.priceInWeiEach)} ETH each.`,
			transactionHash: event.txHash
		}
	} else if (event.action === 'order cancelled') {
		return {
			explanation: `Order cancelled for token: ${projectDetails.name} (${projectDetails.symbol}).`,
			transactionHash: event.txHash
		}
	} else if (event.action === 'order fulfilled') {
		if (event.quantity.eq('0')) {
			// order was completely fulfilled
			return {
				explanation: `Order fulfilled for token: ${projectDetails.name} (${projectDetails.symbol}). TokenId: ${event.optionalTokenId}`,
				transactionHash: event.txHash
			}
		} else {
			// order was partially fulfilled
			return {
				explanation: `Order (partially) fulfilled for token: ${projectDetails.name} (${projectDetails.symbol}). TokenId: ${event.optionalTokenId}. There are ${event.quantity} remaining for this order.}`,
				transactionHash: event.txHash
			}
		}
	} else {
		return {
			explanation: `Error: unable to parse event`,
			transactionHash: event.txHash
		}
	}
}
